.admin-controlbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 32px 0;
}

.controlbar-left {
    display: flex;
    align-items: center;

    h2 {
        font-size: $font-s * 1.5;
        font-weight: 700;
        margin-right: 47px;
    }
}

.add-box {
    p {
        font-weight: 700;

        span {
            color: $link-hover;
            margin-right: 8px;
        }
    }   
}

.controlbar-right {

    .material-icons {
        &:last-child {
            margin-left: 26px;
        }
    }

}

.admin-table {
    margin-bottom: 63px;

    thead {
        border-bottom-color: $grey-900;

        tr {
            th {
                padding-bottom: 4.5px;
                padding-left: 0;

                .material-icons {
                    font-size: $font-s;
                    font-weight: 700;
                    margin-left: 7px;
                    color: $link-hover;
                }
            }
        }
    }

    tbody {
        tr {
            th, td {
                padding: 19px 0 17px;

                .material-icons { 
                    padding-right: 0;  //沒有靠右對齊
                }
            }
        }
    }
}

.pagination {
    justify-content: flex-end;

    .page-link {
        color: $link-hover;
        border-color: $link-hover;
        padding: 6px 12px;
    
        &:first-child, &:last-child {
            padding: 4px; //吃不到第一個和最後一個
        }
    
        .material-icons {
            color: $link-hover;
        }
    }
}

